import React, { useEffect, useState, useRef } from "react";
import {Link} from 'gatsby';
import cx from 'classnames';
import SEO from "../components/seo";
import Navigation from "../components/navigation/navigation.component";
import Me from "../../assets/me-and-kishka.jpg";
import cv from '../../docs/cv-t.json';
import {motion} from 'framer-motion';

const TOOLS = [
    'javascript',
    'react',
    'angular',
    'python',
    'ruby',
    'CSS-3',
    'd3',
    'firebase',
    'typescript',
    'html-5',
    'bulma',
    'aws',
    'google-cloud-platform',
    'git',
    'intellij-idea',
    'emacs',
    'material-ui',
    'nodejs',
];

const IndexPage = () => {
    const [isTitleVisible, setTitleIsVisible] = useState(false);
    const [displaySubtitle, setDisplaySubTitle] = useState(false);
    const [isSubTitleVisible, setSubTitleIsVisible] = useState(false);
    const [displayTimeline, setDisplayTimeline] = useState(false);
    const [displayLastSection, setDisplayLastSection] = useState(false);
    const timelineRef = useRef();
    const variants = {
        hidden: { opacity: 0, },
        visible: { opacity: 1, },
    };
    const variantsWithSize = {
        hidden: { opacity: 0, height: 0 },
        visible: { opacity: 1, height: '100%'},
    };
    const transition = { ease: 'easeIn', duration: .5};

    const complete = () => {
        setTimeout(() => {
            setSubTitleIsVisible(true);
        }, 3500);

    }

    useEffect(() => {
        if (isSubTitleVisible) {
            setTimeout(() => {
                setDisplayTimeline(true);
            }, 3000);
        }
    }, [isSubTitleVisible])

    useEffect(() => {
        setTimeout(() => {
            setDisplaySubTitle(true);
            complete();
        }, 3500);
    })

    useEffect(() => {
        if (displayTimeline && timelineRef && timelineRef.current) {
            const items = timelineRef.current.querySelectorAll('.timeline-item');
            const observer = new IntersectionObserver((entries => {
                entries.forEach(entry => {
                    if (entry.target.id.includes('CFE') && entry.isIntersecting) {
                        setDisplayLastSection(true);
                        items.forEach(item => observer.unobserve(item));
                    }
                })
            }), {threshold: [.9], root: timelineRef.current})

            for (let i = 0; i < items.length; i++) {
                observer.observe(items[i]);
            }
        }
    }, [displayTimeline])

    useEffect(() => {
        if (displaySubtitle) {
            setTitleIsVisible(true);
        }
    }, [displaySubtitle])

    const titleClassNames = cx(
        'is-paddingless has-background-transparent title is-1 is-inline-block letter-spacing', {
            typewriter: !isTitleVisible
        });

    const subtitleClassNames = cx(
        'is-paddingless has-background-transparent is-2 is-inline-block', {
            typewriter: displaySubtitle,
            subtitle: displaySubtitle,
            transparent: !displaySubtitle,
        });

    const experienceContentClassNames = cx(
        'is-shadowless has-background-transparent',
        {
            'is-hidden': !isSubTitleVisible,
        }
    )

    return (
        <>
            <SEO title="Home" />

            <section className="hero is-fullheight hero-custom-background">
                <div className="hero-head">
                    <Navigation />
                </div>

                <div className="hero-body">
                    <div className="container">

                        <div className="columns">
                            <div className="column is-3 is-gapless">
                                <div className="box is-shadowless">
                                    <figure className="image is-1by1">
                                        <img className="" src={Me} />
                                    </figure>
                                </div>
                            </div>
                            <div className="column">
                                <div className="is-inline-block">
                                    <pre className={titleClassNames}>Hello, I'm Jorge Garca</pre>
                                </div>
                                <div className="is-inline-block">
                                    <pre className={subtitleClassNames}>Sr. Software Engineer</pre>
                                </div>

                                {isSubTitleVisible && (
                                    <motion.div
                                        animate="visible"
                                        initial="hidden"
                                        variants={variants}
                                        className="content box has-background-transparent is-inline-block">
                                        <p>
                                            I am a Senior Software Engineer with a passion for good design, team focused, clear communicator, emphasizing cross team collaboration and transparency.
                                            I promote critical discussions on the role and purpose of UI design and layout informed by analytics, research, and prototypes.
                                            I am a pragmatic developer, with a focus on design patterns and practical problem solving
                                        </p>
                                    </motion.div>
                                )}
                            </div>
                        </div>

                        {displayTimeline && (
                            <motion.div
                                animate="visible"
                                initial="hidden"
                                variants={variants}
                                transition={transition}
                                className={experienceContentClassNames}
                                ref={timelineRef}
                            >
                                <h1 className="title is-1 has-text-centered has-text-info">Where I've been contributing</h1>
                                <div style={{height: 300, overflowY: 'scroll', overflowX: 'hidden'}}>
                                    {cv.companies.map((company) => (
                                        <div className="columns timeline-item" id={company.name} key={company.name}>
                                            <div className="column is-4" style={{paddingBottom: 0, paddingTop: 0,  borderRight: '5px solid #2E2659', paddingRight: 0}}>
                                                <h1 className="is-3 title sticky-p" style={{color: '#270160', borderRight: '5px solid #FC006C'}}>
                                                    {company.name}
                                                    <p className="has-text-right subtitle is-6">
                                                        <span className="tag has-text-white is-radiusless" style={{backgroundColor: '#FC006C'}}>
                                                          {company.period}
                                                        </span>
                                                    </p>
                                                </h1>
                                            </div>
                                            <div className="column is-8 is-paddingless">
                                                <div className="has-background-transparent is-shadowless" style={{color: '#270160', minHeight: 320}}>
                                                    <p className="box sticky-p">{company.description}</p>
                                                </div>
                                            </div>

                                        </div>
                                    ))}
                                </div>
                            </motion.div>
                        )}


                        {displayLastSection && (
                            <motion.div
                                animate="visible"
                                initial="hidden"
                                variants={variantsWithSize}
                                transition={transition}
                                className="section">
                                <div className="has-text-centered">
                                    <Link to="/curriculum?extended">
                                        <a className="button is-primary is-uppercase">See the full version</a>
                                    </Link>
                                </div>
                                <br/>
                                <br/>
                                <div className="columns">
                                    <div className="column">
                                        <h1 className="title is-1 has-text-centered has-text-info">I enjoy working with</h1>
                                        <br/>
                                        <div className="tools">
                                            {TOOLS.map(item => {
                                                return (
                                                    <>
                                                        <div className="tool">
                                                            <img className="image is-96x96" src={`/tools-icons/${item.toLowerCase()}.svg`} alt=""/>
                                                            <br/>
                                                            <p className="has-text-centered is-uppercase is-size-7"><span>{item}</span></p>
                                                        </div>
                                                    </>
                                                );
                                            })}
                                        </div>
                                    </div>
                                </div>
                            </motion.div>
                        )}
                    </div>
                </div>
            </section>
            {displayLastSection && (<footer className="footer has-background-black">
                <div className="content has-text-centered">
                    <i><strong>BIT-DRAGON</strong> <small>2020</small></i> made with  by <a href="https://bit-dragon.github.io">Jorge Garcia</a>
                </div>
            </footer>)}
        </>
    );
};

export default IndexPage;
